/* ì „ì²´ ì»¨í…Œì´ë„ˆ */
.work-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding: 20px;
  max-width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
}

/* ì½˜í…ì¸  ì˜ì—­ */
.work-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
  flex: 1;
  align-items: flex-start;
}

.caption-title {
  white-space: pre-wrap;
  word-break: break-word;
  line-height: 1.4;
  font-size: 0.9rem;
  color: #000000;
  margin: 0;
}

/* Masonry ê·¸ë¦¬ë“œ */
.my-masonry-grid {
  display: flex;
  margin-left: -30px; /* gutter size offset */
  width: auto;
}
.my-masonry-grid_column {
  padding-left: 30px; /* gutter size */
  background-clip: padding-box;
}

.thumb {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}
.work-grid-item {
  margin-bottom: 30px;
}

.caption {
  text-align: left;
}

.caption-line-last {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  gap: 8px;
}

.caption-year-inline {
  font-size: 0.95rem;
  white-space: nowrap;
  margin-left: 8px;
  color: #000000;
}

.caption-info {
  font-size: 0.9rem;
  color: #000000;
  margin: 0;
}

.work-link {
  text-decoration: none;
  color: inherit;
  display: block;
}

.footer {
  margin-top: auto;
}

/* ë°˜ì‘í˜• ì¶”ê°€ */
@media (max-width: 1200px) {
  .work-content {
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .caption-title {
    font-size: 0.75rem; /* ë” ì‘ê²Œ! */
    line-height: 1.3;    /* ì¤„ê°„ê²©ë„ ì‚´ì§ ì¤„ì„ */
  }
  .caption-year-inline,
  .caption-info {
    font-size: 0.75rem;  /* ë” ì‘ê²Œ! */
    line-height: 1.6;
  }
}